import React, { useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import {useDispatch} from 'react-redux';
import {registerUser} from '../_actions/user_action'

function SignPage() {
    const navigate = useNavigate();
    const dispatch =useDispatch();
    const [Name, setName] = useState("")
    const [Password, setPassword] = useState("")
    const [ConfirmPassword, setConfirmPassword] = useState("")

    const onNameHandeler = (event) =>{
        setName(event.currentTarget.value)
    }
    const onPasswordHandler = (event) => {
        setPassword(event.currentTarget.value)
    }
    const onConfirmPasswordHandler=(event)=>{
        setConfirmPassword(event.currentTarget.value)
      }
    const onSubmitHandler= (event) =>{
        event.preventDefault();
    
        if(Password !== ConfirmPassword){
          return alert('   !')
        }
        
        let body = {
          name: Name,
          password: Password,
        }
        dispatch(registerUser(body))
            .then(response => {
              console.log(response.payload)
              if(response.payload!=null){
                // props.history.push('/')  
                navigate('/');
              }else{
            //    setFormErrorMessage("   ")
               alert('   ');
                    }
                  })
                  .catch(err => {
                    console.log(err)
                    // setFormErrorMessage('  .')
                    setTimeout(() => {
                    //   setFormErrorMessage("")
                    }, 3000);
                  });
    }
    return (
        <div className="container">
            <form action="" method="post" onSubmit={onSubmitHandler}>
                <div className="form-group">
                    <label htmlFor="name"></label>
                    <input type="text" id="name" name="name" onChange={onNameHandeler} placeholder=" "></input>
                </div>
                <div>
                    <label htmlFor="password"></label>
                    <input type="password" id="password" name="password" onChange={onPasswordHandler} placeholder=" "></input>
                </div>
                <div>
                    <label htmlFor="passwordConfirm"></label>
                    <input type="password" id="passwordConfirm" name="passwordConfirm" onChange={onConfirmPasswordHandler} placeholder=" "></input>
                </div>
                <button onSubmit={onSubmitHandler} ></button>
            </form>
        </div>
        // <div className='Write'>
        //     <form action="/members/new" method="post">
        //         <div>
        //         	<input type='text' id='title_txt' name='name' placeholder=''/> 
        //         </div>
        //         <div>
        //         	<input type='url' id='link_txt' name='url' placeholder=''/> 
        //         </div>
        //         <div>
        //         	<textarea id='ex_txt' name='content' placeholder='  '></textarea>    
        //         </div>
        //         <div>
        //             &nbsp;&nbsp;
        //                 <input type="date" name="startDate" id='date'/>
        //             	&nbsp;&nbsp;

        //             &nbsp;&nbsp;
        //                 <input type="date" name="endDate" id='date'/>    
        //         </div>
        //         <div id="submit_btn">
        //             <button type="submit"></button>&nbsp;&nbsp;
        //             <button></button>
        //         </div>
        //     </form>
        // </div>

    );
}

export default SignPage;